.divContainer {
    display: flex;
   justify-content: center;
  }

.table {
    border-left: solid 15px rgb(6, 6, 104);
    border-right: solid 15px rgb(6, 6, 104);
    border-top: solid 3px rgb(6, 6, 104);
    border-bottom: solid 5px rgb(6, 6, 104);
    background-color: blue;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tbody {
    width: 100%;
    height: 100%;
    background: linear-gradient(-46deg,#6834ca,#0200ad,#552619,#494013);
    background-size: 400% 400%;
    animation: gradient 10s ease infinite;
    font-family: "open sans";
}

@keyframes gradient 
{
    0%
    {
        background-position:0% 50%;
    }
    50%
    {
        background-position:100% 50%;
    }
    100%
    {
        background-position:0% 100%;
    }
}

